<template>
  <!--日常法律咨询/发起咨询-->
  <div style="margin-left: 6%">
    <a-list item-layout="horizontal" :data-source="listData"
            size="large" :pagination="pagination" :loading="isLoading">
      <a-list-item slot="renderItem" key="item.id" slot-scope="item, index">
        <a-list-item-meta
            :description="item.desc"
        >
          <a slot="title" href="https://www.antdv.com/" @click.prevent="$router.push({
             name: 'consultationDetail',
             query: {consultId: item.id }
          })">
            {{ item.title }}
          </a>
        </a-list-item-meta>
        <p slot="extra" style="color: grey;padding-top: 2%">{{ item.createTime }}</p>
      </a-list-item>
    </a-list>
  </div>
</template>

<script>
  import moment from 'moment';
  import 'moment/locale/zh-cn';
  import request from "@/utils/request";
  //moment(endTime).diff(moment(startTime), 'days')
  //moment().format('L');

  export default {
    name: 'MyConsultation',
    data() {
      return {
        listData: [
          {
            id: '001',
            title: 'Ant Design Title 1',
            desc: 'Ant Design, a design language for background applications, is refined by Ant UED Team',
            createTime: '2022-09-10'
          },
          {
            id: '002',
            title: 'Ant Design Title 2',
            desc: 'Ant Design, a design language for background applications, is refined by Ant UED Team',
            createTime: '2022-09-10'
          },
          {
            id: '003',
            title: 'Ant Design Title 3',
            desc: 'Ant Design, a design language for background applications, is refined by Ant UED Team',
            createTime: '2022-09-10'
          },
          {
            id: '004',
            title: 'Ant Design Title 4',
            desc: 'Ant Design, a design language for background applications, is refined by Ant UED Team',
            createTime: '2022-09-10'
          },
        ],
        itemTotal: 0,
        pagination: {
          onChange: page => {
            // console.log(page);
          },
          pageSize: 6,
        },
        moment,
        isLoading: false
      }
    },
    methods: {
      selectByPage(currentPage = 1, pageSize = 6) {
        this.isLoading = true;

        request({
          method: 'get',
          url: '/law/list',
          params: {
            currentPage: currentPage,
            pageSize: pageSize
          }
        }).then(res => {

          if (res.code === 1) {
            // console.log(res.data.total);
            this.listData = res.data.list;
          } else {
            this.$message.error(res.msg);
          }
          this.isLoading = false;

        }).catch(error => {
          this.$message.error('服务器错误！');
          console.log(error);
          this.isLoading = false;
        })

      }
    },

    activated() {
      this.selectByPage();
    }
  }
</script>

